// src/components/weeks/week1/Lecture4.tsx

import React, { useEffect } from 'react';
import {Typography, Box, Button, Paper} from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import { CopyBlock, dracula } from 'react-code-blocks';

const Lecture4: React.FC = () => {
    useEffect(() => {
        const completionStatus = JSON.parse(localStorage.getItem('completionStatus') || '{}');
        completionStatus['lecture4'] = true;
        localStorage.setItem('completionStatus', JSON.stringify(completionStatus));
    }, []);

    return (
        <Paper style={{ padding: 16 }}>
            <Box my={4}>
                <Typography variant="h4" component="h1" gutterBottom>
                    Lecture 4: Naming Conventions for Variables
                </Typography>
                <Typography variant="body1" paragraph>
                    In this lecture, we will discuss the importance of naming conventions for variables in Python. Good naming conventions improve the readability and maintainability of your code.
                </Typography>
                <Typography variant="h5" component="h2" gutterBottom>
                    Why Naming Conventions Matter
                </Typography>

                <Typography variant="body1" paragraph>
                    Consistent naming conventions help other developers understand your code more quickly. It also helps you remember what your variables represent when you come back to your code after some time.
                </Typography>
                <Typography variant="h5" component="h2" gutterBottom>
                    Best Practices for Naming Variables
                </Typography>
                <CopyBlock
                    text={`# Use descriptive names
user_age = 25
product_price = 99.99

# Use underscores for separating words
first_name = "John"
last_name = "Doe"

# Be consistent with naming conventions
is_active = True
num_of_items = 5

# Avoid single character names except for loop variables
for i in range(10):
    print(i)`}
                    language="python"
                    theme={dracula}
                    codeBlock
                />
                <Typography variant="h5" component="h2" gutterBottom>
                    Example of bad naming conventions
                </Typography>
                <CopyBlock
                    text={`# Example with bad naming
pls = {
    "pl1": {"sx": 10, "sy": 15, "sz": 20},
    "pl2": {"sx": 5, "sy": 7, "sz": 12}
}

for p in pls.values():
    s = (p["sx"]**2 + p["sy"]**2 + p["sz"]**2)**0.5
    print(s)`}
                    language="python"
                    theme={dracula}
                    codeBlock
                />
                <Typography variant="h5" component="h2" gutterBottom>
                    Example of good naming conventions
                </Typography>
                <CopyBlock
                    text={`# Example with good naming
planets = {
    "planet1": {"speed_x": 10, "speed_y": 15, "speed_z": 20},
    "planet2": {"speed_x": 5, "speed_y": 7, "speed_z": 12}
}

for planet in planets.values():
    speed_norm = (planet["speed_x"]**2 + planet["speed_y"]**2 + planet["speed_z"]**2)**0.5
    print(speed_norm)`}
                    language="python"
                    theme={dracula}
                    codeBlock
                />
                <Typography variant="body1" paragraph>
                    This code shows how using descriptive names for variables and dictionary keys improves readability and understanding of the code.
                </Typography>
                <Typography variant="body1" paragraph>
                    Follow these best practices to make your code cleaner and more understandable.
                </Typography>
                <Box mt={4}>
                    <Button variant="contained" color="primary" component={RouterLink} to="/week1/lecture4/questionnaire">
                        Take the Questionnaire
                    </Button>
                </Box>
            </Box>
        </Paper>
    );
};

export default Lecture4;
